.seperator-container {
  width: 100vw;
  overflow: hidden;
  z-index: -1;
}

.seperator-bar {
  position: relative;
  align-self: flex-start;
  opacity: 0.3;
  display: flex;
  animation-name: k1;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: backwards;
  margin: calc(30px * 3) 0px calc(30px * 3) 0px;
  margin: calc(var(--margin) * 3) 0px calc(var(--margin) * 3) 0px;
}

.seperator-txt {
  height: auto;
  width: auto;
  white-space: nowrap;
  margin: 0px 10px 0px 10px;
}

@keyframes k1 {
  to {
    transform: translateX(-100%);
  }
}

@media screen and (max-width: 1024px) {
  h1 {
    font-size: 42px;
  }
  h2 {
    font-size: 27px;
  }
  h3 {
    font-size: 14px;
  }
  p,
  i {
    font-size: 12px;
  }
}

